:root {
    --size-icon-rad: calc(var(--size-icon-height) * 3 + var(--size-icon-gap) * 2);
    --size-icon-height: calc(2.4px + (3 - 2.4) * (100vw - 320px) / (1920 - 320));
    --size-icon-width: calc(24px + (24 - 18) * (100vw - 320px) / (1920 - 320));
    --x-width: calc(var(--size-icon-rad) * 1.41421356237);
    --size-icon-gap: calc(3px + (6 - 3) * (100vw - 320px) / (1920 - 320));
}

header {
    position: fixed;
    width: 100%;
    z-index: 10;
    top: 0;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-right: 18px;
    display: flex;
    justify-content: end;
}

.burger-bar {
    display: flex;
    flex-direction: column;
    gap: var(--size-icon-gap);
    cursor: pointer;
    width: max-content;
    background-color: inherit;
}

.burger-bar::before{
    background-color: rgb(249, 220, 210);
}

.burger-bar span{
    background-color: rgb(221, 231, 246);
}

.burger-bar::after{
    background-color: rgb(248, 219, 219);
}

.burger-bar::before,
.burger-bar::after,
.burger-bar span {
    content: "";
    width: var(--size-icon-width);
    height: var(--size-icon-height);
    border-radius: 20px;
    transition: all 0.5s ease-in-out;
    transform-origin: right center;
}

.clicked span {
    width: 0;
    opacity: 0;
}

.clicked::before {
    rotate: -48deg;
    width: var(--x-width);
    translate: 0 calc(var(--size-icon-height) /-2);
}

.clicked::after {
    rotate: 48deg;
    width: var(--x-width);
    translate: 0 calc(var(--size-icon-height) / 2);
}

.menu {
    position: absolute;
    height: 100vh;
    width: 100%;
    z-index: 9;
    background-color: black;

    overflow: hidden;
    text-align: center;
}

.menu-close {
    opacity: 0;
    transition: all 300ms ease-in;
}

.menu-open {
    opacity: 0.9;
    transition: all 300ms ease-in-out;
}

.chakra {
    position: absolute;
    top: -70vw;
    left: 0;
    height: 100vw;
    width: 100vw;
    opacity: 0.8;
    animation-name: spin;
    animation-duration: 50000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.elephant1 {
    position: absolute;
    bottom: -10vw;
    left: -20vw;
    height: 40vw;
    animation-name: elephant1Rotate;
    animation-duration: 5000ms;
    animation-iteration-count: infinite;
}

.elephant2 {
    position: absolute;
    bottom: -10vw;
    right: -20vw;
    height: 40vw;
    animation-name: elephant2Rotate;
    animation-duration: 5000ms;
    animation-iteration-count: infinite;
}

@keyframes elephant1Rotate {
    0% {
        transform: rotate(0) scaleX(-1);
        -webkit-transform: rotate(0) scaleX(-1);
        -ms-transform: rotate(0) scaleX(-1);
    }

    50% {
        transform: rotate(-10deg) scaleX(-1);
        -webkit-transform: rotate(-10deg) scaleX(-1);
        -ms-transform: rotate(-10deg) scaleX(-1);
    }

    100% {
        transform: rotate(0deg) scaleX(-1);
        -webkit-transform: rotate(0deg) scaleX(-1);
        -ms-transform: rotate(0deg) scaleX(-1);
    }
}

@keyframes elephant2Rotate {
    0% {
        transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
    }

    50% {
        transform: rotate(10deg);
        -webkit-transform: rotate(10deg);
        -ms-transform: rotate(10deg);
    }

    100% {
        transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
    }
}

.menu-contents{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* padding-top: 35vw; */
    gap: 16px;
}

.menu h4{
    width: 90vw;
    font-family: 'Dancing Script', cursive;
}

.info{
    position: absolute;
    bottom: 10px;
    width: 100%;
    text-align: center;
    font-weight: 600;
}
